
const  state= {
    currentYear: '',
    currentTitle: '',
    timeLeft: -1,
};
const getters= {
    getCurrentYear: state=> state.currentYear,
    getCurrentTitle: state=> state.currentTitle,
    getTimeLeft: state=> state.timeLeft,
};
const mutations= {
    setCurrentYear: (state,year)=>state.currentYear = year,
    setCurrentTitle: (state,title)=>state.currentTitle = title,
    setTimeLeft: (state,time)=> state.timeLeft = time,
};
const actions= {
    serviceInit: ({commit})=>{
        commit('setCurrentYear', localStorage.getItem('currentYear') || '' );
        commit('setCurrentTitle', localStorage.getItem('currentTitle') || '' );
        commit('setTimeLeft', JSON.parse( localStorage.getItem('timeLeft') ) || -1 );
    },
    setCurrentYear: ({commit},year)=>{
        localStorage.setItem('currentYear',year);
        commit('setCurrentYear',year);
    },
    setCurrentTitle: ({commit},title)=>{
        localStorage.setItem('currentTitle',title);
        commit('setCurrentTitle',title);
    },
    setTimeLeft: ({commit},time)=>{
        localStorage.setItem('timeLeft', JSON.stringify( time ) );
        commit('setTimeLeft',time);
    },
};
export default {
    state, getters, mutations, actions
};